import React from 'react'

const Card = ({id,name,email}) => {
    
            return (
        <div >
            <img src={`https://robohash.org/${id}?100*100`} alt="photo" />
            <div>
                <h1 className="f5 blue">{name}</h1>
                <p>{email}</p>
            </div>
        </div>
    )


   
}
export default Card

