import React, { Component } from 'react'
import './App.css';
import SearchBox from '../Component/SearchBox'
import CardList from '../Component/CardList'
import Scroll from '../Component/Scroll.js'

import 'tachyons'
class App extends Component {
    constructor(){
        super()
        this.state ={
            robots: [],
            searchfield : ''
        }
    }
    componentDidMount(){
fetch('https://jsonplaceholder.typicode.com/users').then(response => response.json())
.then(users =>{ this.setState({robots : users})})
    }
    onsearchchange=(event)=>{
        this.setState({searchfield : event.target.value})
    }
    render() {
        const robotFilter = this.state.robots.filter((index)=>{
           return index.name.toLowerCase().includes(this.state.searchfield.toLowerCase())
        })
        return (
            <div className="tc">
               
                       <h1>Robots Freind</h1>
                      <SearchBox search={this.onsearchchange}/>
              <Scroll>
                <CardList robot={robotFilter}/>
            </Scroll>
            </div>
        )
    }
}

export default App


